import { Era } from "@/types/timelineEvents";
import styles from "./baseline.module.scss";

interface PBaseline {
  start: number;
  era: Era;
}

const Baseline = ({ start, era }: PBaseline) => {
  const currentYear = new Date().getFullYear();
  const years = [start, 0, currentYear];
  return (
    <div
      className={styles.grid}
      style={{ gridTemplateColumns: `repeat(${years.length}, 1fr)` }}
    >
      {years.map((y, index) => (
        <div key={index}>{y}</div>
      ))}
    </div>
  );
};

export default Baseline;
